.wrapper {
  display: flex;
  align-items: center;
  gap: 30px;
  padding: 15px 0;
  justify-content: space-between;
  border-bottom: 1px solid #0b0b0b;
}

.menu {
  list-style: none;
  display: flex;
}

.link {
  padding: 8px 26px;
  border-radius: 4px;
  text-decoration: none;
  color: #0e0e0e;
  background-color: #fff;
  outline: 2px solid #090909;
  font-weight: 500;
  font-size: 18px;
}

.link.active {
  background-color: #090909;
  background-image: linear-gradient(315deg, #ac8b08 0%, #ebc661 74%);
  color: #fff;
  outline: 2px solid #fff;
}

.logo {
  display: flex;
  font-size: 40px;
  font-weight: 700;
  text-align: center;
  align-items: center;
  justify-content: center;
  background: linear-gradient(-70deg, rgb(49, 42, 15), #d4881f);

  -webkit-text-fill-color: transparent;
  background-clip: text;
}